
:root {
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

#App{
  width: 500px;
  height: 800px;
  box-sizing: border-box;
  left: 50%;
  top: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
}
.game-content{
  width: inherit;
  height: inherit;
  box-sizing: border-box;
  position: relative;
}

.scores{
  width: 100%;
  position: absolute;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  display: flex;
  padding: 10px 10px 25px 10px;
}

.energy{
  display: flex;
  margin-right: 10px;
}

.energy > p{
  position: relative;
  top: -15px;
  left: 6px;
  font-family:  "Retro";
  font-size: 15px;
}

.energy > img{
  width: 15px;
  height: 25px;
}

.people{
  display: flex;
  margin-right: 20px;
}

.people > p{
  position: relative;
  top: -15px;
  left: -10px;
  font-family:  "Retro";
  font-size: 15px;
}

.people > img{
  width: 50px;
  height: 45px;
  position: relative;
  top: -12px;
}

.bullet{
  display: flex;
  margin-right: 20px;
  position: relative;
  left: -53px
}

.bullet > p{
  position: relative;
  top: -15px;
  left: -38px;
  font-family:  "Retro";
  font-size: 15px;
}

.bullet > img{
  width: 100px;
  height: 95px;
  position: relative;
  top: -34px;
}

.score{
  width: auto;
  display: flex;
  margin-right: 20px;
  position: absolute;
  right: 50px;
  flex-direction: row-reverse;
}

.score > p{
  position: relative;
  top: -15px;
  font-family:  "Retro";
  font-size: 15px;
  left: 35px;
}

.score > img{
  width: 100px;
  height: 95px;
  position: relative;
  top: -34px;
}


canvas{
  background-color: #88552a;
  width: inherit;
  height: inherit;
  box-sizing: border-box;
  border-radius: 5px;

}